.list {
margin:0 auto;
padding:0;
width:390px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-between;
}
.items{
    padding:5px;
    width:100%;
    display: flex;
    justify-content: space-around;
}
.button {
    padding: 5px 10px;
	outline: none ;
    border: 1px solid rgb(54, 54, 65);
    background: transparent;
   
}
button:hover{
    box-shadow: 0 2px 2px 0px rgba(50, 44, 51, 0.5);

}